import React, { useState } from "react";
import styled from "styled-components";
import Modal from "../modal/modal";
import { GuestAnon, Input } from "./style/guest_anon_pw_style";

function GuestAnonDelete(props) {
  const { closeModal, deleteData, gid } = props;
  const [isModal, setModal] = useState(false);
  const [pw, setPw] = useState("");
  const firstBtn = {
    event: () => {
      setModal(false);
      doDelete();
    },
    value: "",
    style: {
      backgroundColor: "red",
      borderRadius: "5px",
      border: "1px solid rgba(50, 50, 50, 0.5)",
    },
  };
  const secondBtn = {
    event: () => {
      setModal(false);
    },
    value: "",
    style: {
      backgroundColor: "grey",
      borderRadius: "5px",
      border: "1px solid rgba(50, 50, 50, 0.5)",
    },
  };
  const modalStyle = {
    height: "130px",
  };

  const handlePw = (e) => {
    setPw(e.target.value);
  };

  const doDelete = () => {
    closeModal();
    deleteData({ gid, password: pw });
  };

  return (
    <GuestAnon>
      {isModal && (
        <Modal
          title={""}
          firstBtn={firstBtn}
          secondBtn={secondBtn}
          style={modalStyle}
          zIndex={150}
          closeModal={() => setModal(false)}
        >
          <div style={{ fontSize: "15px" }}> ?</div>
        </Modal>
      )}
      <div className="pwInputs">
        <Input
          type="password"
          onChange={handlePw}
          value={pw}
          placeholder=" "
        ></Input>
      </div>
      {pw.length >= 8 && (
        <div className="submitBtn" onClick={() => setModal(true)}>
          
        </div>
      )}
      {pw.length < 8 && <div className="submitBtnDisabled"></div>}
    </GuestAnon>
  );
}

export default GuestAnonDelete;
